export default {
	name: 'tuiIcon',
	props: {
		name: {
			type: String,
			default: ''
		},
		size: {
			type: Number,
			default: 32
		},
		color: {
			type: String,
			default: '#999'
		},
		bold: {
			type: Boolean,
			default: false
		},
		margin: {
			type: String,
			default: "0"
		},
		index: {
			type: Number,
			default: 0
		}
	},
	methods: {
		handleClick() {
			this.$emit('click', {
				index: this.index
			});
		}
	}
};
